<style scoped>
.breadcrumb {
  margin-bottom: 20px;
}

.banner-list {
  padding: 15px;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.table-list {
  overflow: auto;
  flex: 1;
}

.banner-operation {
  margin-bottom: 20px;
}

.pages {
  text-align: right;
}
</style>


<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="banner-list">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="banner-operation">
      <el-button type="primary" @click="addGuide"></el-button>
      <!-- <el-button type="danger" @click="deleteGuide"></el-button> -->
    </div>
    <div class="table-list">
      <el-table ref="multipleTable" :data="guideLists" border stripe tooltip-effect="dark" style="width: 100%" @selection-change="handleSelection">
        <!-- <el-table-column type="selection">
        </el-table-column> -->
        <el-table-column label="ID" prop="id" sortable>
        </el-table-column>
        <el-table-column label="" prop="title">
        </el-table-column>
        <el-table-column label="" prop="sort" sortable>
        </el-table-column>
        <el-table-column prop="updated_at" label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- <div class="pages">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="page" :page-size="eachPage" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div> -->
  </section>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      //
      waittingData: [],

      //
      guideLists: [],
      page: 1,
      eachPage: 100,
      count: 1000
    };
  },

  created() {
    this.$api.getGuides(res => {
      this.guideLists = res.data.data;
      this.loading = false;
    });
  },

  methods: {
    /*
        
    */
    addGuide() {
      this.$router.push({ name: "guideedit", params: { guide: null } });
    },

    // deleteGuide() {
    //   if (!this.waittingData.length) {
    //     this.$message({
    //       type: "warning",
    //       message: "",
    //       showClose: true
    //     });
    //     return false;
    //   }
    //   this.$confirm(", ?", "", {
    //     confirmButtonText: "",
    //     cancelButtonText: "",
    //     type: "warning"
    //   })
    //     .then(() => {
    //       this.$message({
    //         type: "success",
    //         message: "!"
    //       });
    //     })
    //     .catch(() => {
    //       this.$message({
    //         type: "info",
    //         message: ""
    //       });
    //     });
    // },

    /*
        
    */
    handleSelection(selection) {
      this.waittingData = selection;
    },

    /*
        
    */
    handleEdit(index, row) {
      this.$router.push({ name: "guideedit", params: { guide: row } });
    },

    /*
        
    */
    handleDelete(index, row) {
      this.$operation.tableMessageBox("", () => {
        this.$api.delGuide(row.id, res => {
          this.guideLists.splice(index, 1);
          this.$message({
            type: "success",
            message: "!"
          });
        });
      });
    }

    /*
        
    */
    // handleCurrentChange() {},
  }
};
</script>



