<style>
#root {
  height: 100%;
  display: flex;
  flex-direction: column;
}

/*  */

#header {
  height: 64px;
  padding-left: 30px;
  padding-right: 30px;
  background-color: #324157;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-staff {
  cursor: pointer;
  color: #fff;
}

.header-staff .welcome {
  margin-right: 20px;
}

/*  */

#content {
  flex: 1;
  position: relative;
}

.flex-bug {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
}

.nav-wrap {
  height: 100%;
  background-color: #324157;
  overflow: auto;
}

.nav-wrap-ul {
  width: 200px;
}

.content-router {
  height: 100%;
  flex: 1;
  overflow: hidden;
}

.loading {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading i {
  font-size: 30px;
}

.el-table__body-wrapper {
  overflow: hidden !important;
}

/*  */

#footer {
  background-color: #324157;
  color: #fff;
  text-align: center;
  font-size: 14px;
  padding-top: 10px;
  padding-bottom: 10px;
}
</style>

<template>
  <div id="root">

    <!--  -->
    <header id="header">
      <div class="header-logo">Logo</div>
      <el-dropdown class="header-staff">
        <span class="el-dropdown-link">
          <i class="welcome"></i> SHINING</span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="logout"></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </header>
    <!-- / -->

    <!--  -->
    <section id="content">
      <div class="flex-bug">
        <!--  -->
        <nav class="nav-wrap">
          <el-menu :default-active="routerActive" class="nav-wrap-ul" theme="dark" :unique-opened="true" :router="true">
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-document"></i>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/logoedit">LOGO</el-menu-item>
                <el-menu-item index="/bannerlist"></el-menu-item>
                <el-menu-item index="/textlist"></el-menu-item>
                <el-menu-item index="/guidelist"></el-menu-item>
                <!-- <el-menu-item index="/citylist"></el-menu-item> -->
              </el-menu-item-group>
              <el-submenu index="1-1">
                <template slot="title"></template>
                <el-menu-item index="/paylist"></el-menu-item>
                <!-- <el-menu-item index="/backcharacterlist"></el-menu-item> -->
              </el-submenu>
              <el-submenu index="1-2">
                <template slot="title"></template>
                <el-menu-item index="/reportlist"></el-menu-item>
                <el-menu-item index="/report/type/list"></el-menu-item>
              </el-submenu>
              <el-submenu index="1-3">
                <template slot="title"></template>
                <!-- <el-menu-item index="/backlist"></el-menu-item> -->
                <el-menu-item index="/backcharacterlist"></el-menu-item>
              </el-submenu>
            </el-submenu>
            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-star-on"></i>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/memberlevellist"></el-menu-item>
                <!-- <el-menu-item index="/qrcontrol"></el-menu-item> -->
                <el-menu-item index="/parttimelist"></el-menu-item>
                <el-menu-item index="/userlist"></el-menu-item>
              </el-menu-item-group>
              <el-submenu index="2-1">
                <template slot="title"></template>
                <el-menu-item index="/membercheckin"></el-menu-item>
                <el-menu-item index="/memberscan"></el-menu-item>
                <el-menu-item index="/membershare"></el-menu-item>
              </el-submenu>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="el-icon-message"></i>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/categorylist"></el-menu-item>
                <!-- <el-menu-item index="/chargemode"></el-menu-item> -->
                <!-- <el-menu-item index="/sms"></el-menu-item> -->
                <el-menu-item index="/startpagelist"></el-menu-item>
                <el-menu-item index="/config"></el-menu-item>
              </el-menu-item-group>
              <el-submenu index="3-1">
                <template slot="title"></template>
                <el-menu-item index="/auditList"></el-menu-item>
                <el-menu-item index="/auditpasslist"></el-menu-item>
                <el-menu-item index="/auditcondition"></el-menu-item>
              </el-submenu>
            </el-submenu>
          </el-menu>
        </nav>
        <!-- / -->

        <!--  -->
        <section class="content-router">
          <router-view></router-view>
        </section>
        <!-- / -->
      </div>
    </section>
    <!-- / -->

    <!--  -->
    <footer id="footer"> 2017 &copy; Sennki All Rights Reserved</footer>
    <!-- / -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      routerActive: this.$route.path
    };
  },

  created() {},

  methods: {
    /*
      
    */
    logout() {
      sessionStorage.clear("username");
      this.$router.push("/login");
    }
  }
};
</script>


