<style scoped>
.level-edit-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 60px;
}

.level-edit-form {
  width: 600px;
  margin: 0 auto;
}
</style>

<template>
  <section class="level-edit-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{'path': '/memberlevellist'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="level-edit-form">
      <el-form label-position="top" label-width="80px" :model="levelForm" :rules="rules" ref="levelForm">
        <el-form-item label="" prop="level">
          <el-select v-model="levelForm.level" placeholder="">
            <el-option v-for="item in levels" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="levelForm.description" placeholder="">
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model.number="levelForm.price" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="time">
          <el-input v-model.number="levelForm.time" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="send_daily">
          <el-input v-model.number="levelForm.send_daily" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="send_max">
          <el-input v-model.number="levelForm.send_max" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('levelForm')" style="width: 100%;margin-top: 10px;"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      levels: this.$common.memberLevels,
      levelForm: {
        level: null,
        price: "",
        time: "",
        send_daily: "",
        send_max: "",
        description: ""
      },
      rules: {
        level: [{ required: true, message: "" }],
        description: [{ required: true, message: "" }],
        price: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ],
        time: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ],
        send_daily: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ],
        send_max: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ]
      }
    };
  },

  created() {
    const params = this.$route.params.level;
    if (params) {
      this.levelForm = params;
      this.levelForm.time = params.time / 3600;
    }
  },
  methods: {
    /*
    * 
    */
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$api.memberLevelEdit(this.levelForm, () => {
            this.$message({
              type: "success",
              message: "",
              showClose: true
            });
            this.$router.push("/memberlevellist");
          });
        } else {
          this.$message({
            type: "warning",
            message: "",
            showClose: true
          });
          return false;
        }
      });
    }
  }
};
</script>



