<style scoped>
.charge-mode-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 20px;
}

.charge-mode-operation {
  margin-bottom: 20px;
}

.table-list {
  min-height: 250px;
  overflow: auto;
}
</style>

<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="charge-mode-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="charge-mode-operation">
      <el-tooltip :content="'' + switchText[chargeSwitch]" placement="top">
        <el-switch v-model="chargeSwitch" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0" on-text="" off-text="" @change="chargeModeSwitchFnc">
        </el-switch>
      </el-tooltip>
    </div>

    <div class="table-list">
      <el-table :data="chargeList" border stripe style="width: 100%">
        <el-table-column prop="id" label="ID">
        </el-table-column>
        <el-table-column prop="name" label="">
        </el-table-column>
        <!-- <el-table-column prop="mode" label="">
          <template slot-scope="scope">
            <span v-if="scope.$index != currentIndex">{{chargeMode[scope.row.mode].name}}</span>
            <el-select v-else v-model="scope.row.mode" placeholder="">
              <el-option v-for="item in chargeMode" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </template>
        </el-table-column> -->
        <el-table-column prop="money" label="">
          <template slot-scope="scope">
            <span v-if="scope.$index != currentIndex">{{scope.row.money}} </span>
            <el-input v-else placeholder="" v-model="scope.row.money">
              <template slot="append"></template>
            </el-input>
          </template>
        </el-table-column>
        <el-table-column prop="points" label="">
          <template slot-scope="scope">
            <span v-if="scope.$index != currentIndex">{{scope.row.points}} </span>
            <el-input v-else placeholder="" v-model="scope.row.points">
              <template slot="append"></template>
            </el-input>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">{{statusText[scope.row.status]}}</template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
            <el-button v-if="scope.$index == currentIndex" type="success" size="small" @click="chargeFinish(scope.$index, scope.row)"></el-button>
            <el-button v-else type="primary" size="small" @click="chargeEdit(scope.$index, scope.row)"></el-button>
            <!-- <el-button v-if="scope.row.status == 1" type="warning" size="small" @click="chargeClose(scope.$index, scope.row)"></el-button>
            <el-button v-else type="primary" size="small" @click="chargeOpen(scope.$index, scope.row)"></el-button> -->
          </template>
        </el-table-column>
      </el-table>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      currentIndex: null,
      chargeMode: this.$common.chargeMode,
      switchText: ["", ""],

      chargeSwitch: "1",
      statusText: ["", ""],

      chargeList: [
        {
          id: 1,
          name: "",
          mode: 1,
          money: 21,
          points: 11,
          status: 1
        },
        {
          id: 2,
          name: "",
          mode: 0,
          money: 3,
          points: 11,
          status: 1
        }
      ]
    };
  },

  created() {
    setTimeout(() => {
      this.loading = false;
    }, 200);
  },

  methods: {
    /*
    * 
    */
    chargeModeSwitchFnc(value) {
      console.log(value);
      this.$message({
        type: "info",
        message: `${this.switchText[value]}`,
        showClose: true
      });
    },

    /*
    * 
    */
    chargeEdit(index, row) {
      this.currentIndex = index;
    },

    /*
    * 
    */
    chargeFinish(index, row) {
      this.currentIndex = null;
    },

    /*
    * 
    */
    // chargeClose(index, row) {
    //   this.$operation.tableMessageBox("", () => {
    //     this.$message({
    //       type: "success",
    //       message: "!"
    //     });
    //     this.chargeList[index].status = 0;
    //   });
    // },

    /*
    * 
    */
    // chargeOpen(index, row) {
    //   this.chargeList[index].status = 1;
    // }
  }
};
</script>



