<style scoped>
.back-edit-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 60px;
}

.back-edit-form {
  width: 600px;
  margin: 0 auto;
}
</style>


<template>
  <section class="back-edit-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{'path': '/backlist'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="back-edit-form">
      <el-form label-position="top" label-width="80px" :model="adminUser" :rules="rules" ref="adminUser">
        <el-form-item label="" prop="name">
          <el-input v-model="adminUser.name" placeholder=""></el-input>
        </el-form-item>        
        <el-form-item label="" prop="username">
          <el-input v-model="adminUser.username" placeholder=""></el-input>
        </el-form-item>        
        <el-form-item label="" prop="phone">
          <el-input v-model.number="adminUser.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="adminUser.password" placeholder="6"></el-input>
        </el-form-item>
        <el-form-item label="" prop="character">
            <el-select v-model="adminUser.character" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('adminUser')" style="width: 100%;margin-top: 10px;"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      adminUser: {
        name: "",
        username: "",
        phone: null,
        password: "",
        authorities: [],
        character: ""
      },

      //
      checkAll: true,
      authorities: this.$common.authorities,
      authoritiesArr: [],
      isIndeterminate: true,

      rules: {
        name: [
          { required: true, message: "" },
          { min: 0, max: 10, message: " 10 " }
        ],
        username: [{ required: true, message: "" }],
        phone: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ],
        password: [
          { required: true, message: "" },
          { min: 6, message: " 6 " }
        ],
        character: [{ required: true, message: "", trigger: "change" }]
      }
    };
  },

  created() {
    const user = this.$route.params.user;
    if (user) {
      this.adminUser.name = user.name;
      this.adminUser.password = user.password;
      this.adminUser.authorities = user.authority;
    }

    let arr = [];
    for (let i = 0; i < this.$common.authorities.length; i++) {
      arr.push(i);
    }
    this.authoritiesArr = arr;
  },

  methods: {

    /*
    * 
    */
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$message({
            type: "success",
            message: "",
            showClose: true
          });
        } else {
          this.$message({
            type: "warning",
            message: "",
            showClose: true
          });
          return false;
        }
      });
    }
  }
};
</script>



