<style scoped>
.start-page-list-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.breadcrumb {
  margin-bottom: 20px;
}

.start-page-list-operation {
  margin-bottom: 20px;
}

.table-list {
  min-height: 250px;
  flex: 1;
  overflow: auto;
}
</style>

<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="start-page-list-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="start-page-list-operation">
      <el-button type="primary" @click="addStartPage"></el-button>
    </div>

    <div class="table-list">
      <el-table :data="startPageList" border stripe style="width: 100%">
        <el-table-column prop="id" label="ID" sortable>
        </el-table-column>
        <el-table-column prop="title" label="">
        </el-table-column>
        <el-table-column prop="url" label="" show-overflow-tooltip>
          <template slot-scope="scope">
            <a :href="scope.row.url" target="_blank">{{scope.row.url}}</a>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="link_url" label="" show-overflow-tooltip>
        </el-table-column> -->
        <el-table-column prop="state" label="" sortable>
          <template slot-scope="scope">
            <span v-if="scope.row.state == 0" class="warning"></span>
            <span v-else class="success"></span>
          </template>
        </el-table-column>
        <el-table-column label="" width="200">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button v-if="scope.row.state == 0" type="primary" size="small" @click="chargeOpen(scope.$index, scope.row)"></el-button>
            <!-- <el-button v-else type="warning" size="small" @click="chargeClose(scope.$index, scope.row)"></el-button> -->
            <el-button type="danger" size="small" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      loading: true,

      startPageList: []
    };
  },

  created() {
    this.$api.getLaunchImg(res => {
      this.startPageList = res.data.data;
      this.loading = false;
    });
  },

  methods: {
    /*
      
    */
    addStartPage() {
      this.$router.push({
        name: "startpageedit",
        params: { startPage: null }
      });
    },

    /*
    * 
    */
    handleEdit(index, row) {
      this.$router.push({ name: "startpageedit", params: { startPage: row } });
    },

    /*
    * 
    */
    // chargeClose(index, row) {
    //   this.$confirm(", ?", "", {
    //     confirmButtonText: "",
    //     cancelButtonText: "",
    //     type: "warning"
    //   })
    //     .then(() => {
    //       this.$api.closeLaunchImg(row.id, res => {
    //         this.$message({
    //           type: "warning",
    //           message: ""
    //         });
    //         this.startPageList[index].status = 0;
    //       });
    //     })
    //     .catch(() => {
    //       this.$message({
    //         type: "info",
    //         message: ""
    //       });
    //     });
    // },

    /*
    * 
    */
    chargeOpen(index, row) {
      this.$operation.tableMessageBox(", ?", () => {
        this.$api.closeLaunchImg(row.id, res => {
          this.$api.getLaunchImg(res => {
            this.$message({
              type: "success",
              message: "!"
            });
            this.startPageList = res.data.data;
            this.loading = false;
          });
        });
      });
    },

    /*
      
    */
    handleDelete(index, row) {
      this.$operation.tableMessageBox("", () => {
        this.$api.deleteLaunchImg(row.id, res => {
          this.$message({
            type: "success",
            message: ""
          });
          this.startPageList.splice(index, 1);
        });
      });
    }
  }
};
</script>



