<style>
.audit-list-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.breadcrumb {
  margin-bottom: 20px;
}

.table-list {
  flex: 1;
  overflow: auto;
}

.audit-list-wrap .el-input-group__prepend {
  width: 200px;
}

.audit-list-operation {
  margin-bottom: 20px;
  text-align: right;
}

.audit-list-picker {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  text-align: left;
}

.audit-list-picker > div {
  margin-right: 20px;
}

.audit-pass-list-expand {
  font-size: 0;
}

.audit-pass-list-expand label {
  width: 90px;
  color: #99a9bf;
}

.audit-pass-list-expand .el-form-item {
  margin-right: 0 !important;
  margin-bottom: 0;
  width: 50%;
}

.pages {
  text-align: right;
}
</style>

<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="audit-list-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="audit-list-operation">
      <div style="text-align:right;">
        <el-input placeholder="" v-model="selectInput" style="margin-bottom: 20px;width: 900px;" @keyup.enter.native="selectSearch">
          <el-select v-model="select" slot="prepend" placeholder="">
            <el-option label="" value="username"></el-option>
            <el-option label="" value="user_id"></el-option>
          </el-select>
          <el-button slot="append" icon="search" @click="selectSearch"></el-button>
        </el-input>
      </div>
      <div class="audit-list-picker">
        <!-- <el-transfer filterable :filter-method="filterMethod" filter-placeholder="" :titles="['', '']" :button-texts="['', '']" v-model="searchForm.city" :data="cities">
        </el-transfer> -->

          <el-select v-model="cityForm.sheng" placeholder="" @change="selectSheng">
            <el-option v-for="(item,index) in shengs" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        <!-- </el-form-item> -->
        <!-- <el-form-item label=""> -->
          <el-select v-model="cityForm.shi" placeholder="" @change="selectShi">
            <el-option v-for="(item,index) in shis" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        <!-- </el-form-item> -->
        <!-- <el-form-item label=""> -->
          <el-select v-model="cityForm.xian" placeholder=""  @change="selectXian">
            <el-option v-for="(item,index) in xians" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        <el-date-picker v-model="dateRange" type="daterange" align="right" placeholder="" :picker-options="dateOptions">
        </el-date-picker>
        <!-- <el-select v-model="searchForm.status" filterable placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
        <el-select v-model="searchForm.status" filterable placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select> -->
        <el-button type="primary" icon="search" @click="pickerSearch"></el-button>
      </div>
    </div>
    <div class="table-list">
      <el-table ref="multipleTable" :data="auditList" border stripe tooltip-effect="dark" style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="audit-pass-list-expand">
              <el-form-item label="">
                <el-button type="text" @click="handlePeople(scope.$index, scope.row)">{{ props.row.username }}</el-button>
              </el-form-item>
              <el-form-item label="QQ">
                <span>{{ props.row.QQ || '' }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.WeChat || '' }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.phone || '' }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.read_number }} </span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.passName }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.address }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="ID" prop="id">
        </el-table-column>
        <el-table-column prop="title" label="" show-overflow-tooltip>
          <template slot-scope="scope">
            <el-button type="text" @click="goToInfo(scope.row)">{{scope.row.title}}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="price" label="">
          <template slot-scope="scope">
            <span>{{scope.row.price}} </span>
          </template>
        </el-table-column>
        <el-table-column prop="type" label="">
        </el-table-column>
        <el-table-column prop="created_at" label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span class="normal" v-if="scope.row.created_at == scope.row.updated_at"></span>
            <span v-else class="warning"></span>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="status" label="">
          <template slot-scope="scope">{{statusText[scope.row.status]}}</template>
        </el-table-column> -->
        <el-table-column label="" width="200">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="handlePass(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="danger" @click="handleReject(scope.$index, scope.row)"></el-button>
            <!-- <el-button size="small" type="warning" @click="handleDelay(scope.$index, scope.row)"></el-button> -->
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="page" :page-size="eachPage" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>

    <el-dialog title="" :visible.sync="rejectDialog" center>
      <div style="text-alige: center;">
        <el-select v-model="reason" multiple placeholder="">
          <el-option v-for="item in refuses" :key="item.id" :label="item.title" :value="item.id">
          </el-option>
        </el-select>
        <el-button style="margin-left: 20px;" type="text" @click="getMoreRefuse">{{refuseFlag?'':''}}</el-button>
        <el-button style="display:block; margin: 30px auto 0;width: 300px;" type="primary" @click="confirmRefuse"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
export default {
  data() {
    // const generateCities = _ => {
    //   const data = [];
    //   const cities = ["", "", "", "", "", "", ""];
    //   cities.forEach((city, index) => {
    //     data.push({
    //       label: city,
    //       key: index,
    //       cities: cities[index]
    //     });
    //   });
    //   return data;
    // };
    return {
      loading: true,
      rejectDialog: false,

      cityForm: {
        sheng: "",
        shi: "",
        xian: ""
      },
      reason: [],

      refuses: [],
      refusePage: 1,
      refuseFlag: false,
      currentRefuseId: "",
      currentRefuseIndex: "",

      shengs: [],
      shis: [],
      xians: [],

      // editText: ["", ""],
      // statusText: ["", ""],

      selectInput: "",
      select: "username",

      searchForm: {
        // status: "",
        // dateRange: "",
        city_id: ""
      },
      dateRange: "",
      // cities: generateCities(),
      // filterMethod(query, item) {
      //   return item.cities.indexOf(query) > -1;
      // },

      dateOptions: this.$common.dateOptions,

      //
      page: 1,
      eachPage: 10,
      count: 0,

      //
      auditList: []
    };
  },

  created() {
    this.$api.getUsDistrict("", res => {
      this.shengs = res.data.data;
    });
    this.$api.getUnPassList("", res => {
      this.auditList = res.data.data;
      this.count = res.data.count;
      this.loading = false;
    });
    this.$api.getRefuse("", res => {
      this.refuses = res.data.data;
    });
  },

  methods: {
    //
    goToInfo(row) {
      this.$router.push({ name: "auditsingle", params: { info: row, state: 0 } });
    },
    //
    confirmRefuse() {
      this.$api.changePass(
        this.currentRefuseId,
        { pass: 2, reason: this.reason },
        res => {
          this.$message({
            type: "success",
            message: ""
          });
          this.auditList.splice(this.currentRefuseIndex, 1);
          this.rejectDialog = false;
        }
      );
    },

    //
    getMoreRefuse() {
      this.$api.getRefuse({ page: ++this.refusePage }, res => {
        if (res.data.data.length) {
          this.refuses = [...this.refuses, ...res.data.data];
        } else {
          this.refuseFlag = true;
        }
      });
    },
    /*
    * 
    */

    selectSheng(id) {
      this.cityForm.shi = "";
      this.cityForm.xian = "";
      this.xians = [];
      this.currentArea = this.shengs[this.cityForm.sheng];
      this.searchForm.city_id = id;
      this.$api.getUsDistrict({ pid: id }, res => {
        this.shis = res.data.data;
      });
    },

    /*
    * 
    */

    selectShi(id) {
      const shi = this.cityForm.shi;
      if (!shi) {
        return false;
      }
      this.currentArea = this.shis[shi];
      this.searchForm.city_id = id;
      this.$api.getUsDistrict({ pid: id }, res => {
        this.xians = res.data.data;
      });
    },

    /*
    * 
    */

    selectXian(id) {
      const xian = this.cityForm.xian;
      if (!xian) {
        return false;
      }
      this.currentArea = this.xians[xian];
      this.searchForm.city_id = id;
    },
    /*
    * 
    */
    handlePeople(index, row) {},

    /*
    * input 
    */
    selectSearch() {
      const getData = {
        [this.select]: this.selectInput
      };
      this.$api.getPassList(getData, res => {
        this.auditList = res.data.data;
        this.count = res.data.count;
      });
    },

    /*
    * form 
    */
    pickerSearch() {
      let getData = {};
      if (this.dateRange) {
        getData.start = new Date(this.dateRange[0]).toLocaleDateString();
        getData.end = new Date(this.dateRange[1]).toLocaleDateString();
      }
      getData.city_id = this.searchForm.city_id;
      this.$api.getPassList(getData, res => {
        this.auditList = res.data.data;
        this.count = res.data.count;
      });
    },

    /*
    * 
    */
    handlePass(index, row) {
      this.$operation.tableMessageBox("", () => {
        this.$api.changePass(row.id, { pass: 1 }, res => {
          this.$message({
            type: "success",
            message: "!"
          });
          this.auditList.splice(index, 1);
        });
      });
    },

    /*
    * 
    */
    handleReject(index, row) {
      // this.$prompt("", "", {
      //   confirmButtonText: "",
      //   cancelButtonText: "",
      //   beforeClose: (action, instance, done) => {
      //     if (action === "confirm") {
      //       if (!instance.inputValue) {
      //         this.$message({
      //           type: "warning",
      //           message: "",
      //           showClose: true
      //         });
      //         return false;
      //       }
      //       done();
      //     } else {
      //       done();
      //     }
      //   }
      // })
      // .then(() => {
      //   this.auditList.splice(index, 1);
      //   this.$message({
      //     type: "success",
      //     message: ""
      //   });
      // })
      // .catch(() => {
      //   this.$message({
      //     type: "info",
      //     message: ""
      //   });
      // });
      this.rejectDialog = true;
      this.currentRefuseId = row.id;
      this.currentRefuseIndex = index;
      this.reason = [];
    },

    /*
    * 
    */
    // handleDelay(index, row) {
    //   this.$operation.tableMessageBox("", () => {
    //     this.$message({
    //       type: "success",
    //       message: "!"
    //     });
    //     this.auditList[index].status = 1;
    //   });
    // },

    /*
    * 
    */
    handleCurrentChange(page) {
      let getData = {
        page: page
      };
      if (this.selectInput) {
        getData[this.select] = this.selectInput;
      } else {
        getData.city_id = this.searchForm.city_id;
        getData.start = new Date(this.dateRange[0]).toLocaleDateString();
        getData.end = new Date(this.dateRange[1]).toLocaleDateString();
      }
      this.$api.getPassList(getData, res => {
        this.auditList = res.data.data;
      });
    }
  }
};
</script>



