import {
  Message,
  MessageBox
} from 'element-ui'
import 'element-ui/lib/theme-default/index.css'

export default {

  /**
   * 
   * @param {*} msg 
   * @param {*} callback 
   */
  tableMessageBox(msg, callback) {
    MessageBox.confirm(msg + ", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
      .then(() => {
        typeof callback === 'function' && callback()
      })
      .catch(() => {
        Message({
          type: "info",
          message: ""
        });
      });
  }
}



