<style>
.report-wrap {
  height: 100%;
  padding: 15px;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 20px;
}

.pay-operation {
  margin-bottom: 20px;
}

.pay-picker {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.pay-picker > div {
  margin-right: 20px;
}

.report-wrap .table-list {
  flex: 1;
  overflow: auto;
}

.report-wrap .el-input-group__prepend {
  width: 200px;
}

.table-list {
  flex: 1;
}

.pages {
  text-align: right;
}
</style>


<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="report-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="pay-operation">
      <!-- <el-tooltip :content="': ' + pay_type" placement="top" style="margin-bottom: 20px;">
        <el-switch v-model="pay_type" off-color="#13ce66" on-value="" off-value="" on-text="" off-text="" :width="80">
        </el-switch>
      </el-tooltip> -->
      <div class="pay-picker">
        <el-select v-model="searchForm.pay_type" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="2"></el-option>
          <el-option label="" value="3"></el-option>
        </el-select>
        <el-date-picker v-model="dateRange" type="daterange" align="right" placeholder="" :picker-options="dateOptions">
        </el-date-picker>
        <el-select v-model="searchForm.level" multiple filterable placeholder="">
          <el-option v-for="item in levels" :key="item.id" :label="item.name" :value="item.id">
          </el-option>
        </el-select>
        <!-- <el-transfer filterable :filter-method="filterMethod" filter-placeholder="" :titles="['', '']" :button-texts="['', '']" v-model="city" :data="cities">
        </el-transfer> -->
        <el-button type="primary" icon="search" @click="pickerSearch"></el-button>
      </div>
    </div>

    <div class="table-list">
      <el-table :data="payList" border style="width: 100%; margin-top: 20px">
        <el-table-column prop="id" label="ID" sortable>
        </el-table-column>
        <el-table-column prop="username" label="">
        </el-table-column>
        <el-table-column prop="number" label="">
        </el-table-column>
        <el-table-column prop="pay_type" label="" sortable>
          <template slot-scope="scope">
            <span class="success" v-if="scope.row.pay_type == 3"></span>
            <span class="info" v-else></span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="" prop="level" sortable>
          <template slot-scope="scope">{{levels[scope.row.level].name}}</template>
        </el-table-column> -->
        <el-table-column prop="title" label="" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="price" label="" sortable>
          <template slot-scope="scope">
            <span>{{scope.row.price}} </span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="pages">
      <el-pagination  @current-change="handleCurrentChange" :current-page.sync="page" :page-size="eachPage" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>

  </section>
</template>

<script>
export default {
  data() {
    // const generateCities = _ => {
    //   const data = [];
    // const cities = ["", "", "", "", "", "", ""];
    //   cities.forEach((city, index) => {
    //     data.push({
    //       label: city,
    //       key: index,
    //       cities: cities[index]
    //     });
    //   });
    //   return data;
    // };
    return {
      loading: true,

      searchForm: {
        level: "",
        pay_type: "",
        start: "",
        end: ""
      },
      dateRange: "",

      count: 0,
      page: 1,
      eachPage: 10,

      //
      levels: this.$common.memberLevels,

      // city: "",

      // cities: generateCities(),
      city: [],
      // filterMethod(query, item) {
      //   return item.cities.indexOf(query) > -1;
      // },

      dateOptions: this.$common.dateOptions,

      payList: []
    };
  },

  created() {
    this.$api.getPayOrders("", res => {
      this.payList = res.data.data;
      this.count = res.data.count;
      this.loading = false;
    });
  },

  methods: {
    /*
    * 
    */
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        } else if (column.property === "cost") {
          const values = data.map(item => Number(item.cost));
          sums[index] = values.reduce((sum, value) => {
            return sum + value;
          }, 0);
          sums[index] += " ";
        } else {
          sums[index] = "/";
        }
      });

      return sums;
    },

    /*
    * 
    */
    pickerSearch() {
      if (this.dateRange) {
        this.searchForm.start = new Date(
          this.dateRange[0]
        ).toLocaleDateString();
        this.searchForm.end = new Date(this.dateRange[1]).toLocaleDateString();
      }
      this.$api.getPayOrders(this.searchForm, res => {
        this.payList = res.data.data;
      });
    },

    //
    handleCurrentChange(page) {
      this.$api.getPayOrders(this.searchForm, res => {
        this.payList = res.data.data;
      });
    }
  }
};
</script>



