<style scoped>
.check-in-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 60px;
}

.check-in-form {
  width: 500px;
  margin: 0 auto;
}
</style>

<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="check-in-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="check-in-form">
      <el-form label-position="top" label-width="80px" :model="configForm" :rules="rules" ref="configForm">
        <el-form-item label="" prop="need_pay">
          <el-tooltip :content="'' + checkSwitchText[configForm.need_pay]" placement="top">
            <el-switch v-model="configForm.need_pay" on-color="#13ce66" off-color="#ff4949" :on-value="1" :off-value="0" on-text="" off-text="">
            </el-switch>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="show_sign">
          <el-tooltip :content="'' + checkSwitchText[configForm.show_sign]" placement="top">
            <el-switch v-model="configForm.show_sign" on-color="#13ce66" off-color="#ff4949" :on-value="1" :off-value="0" on-text="" off-text="">
            </el-switch>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="show_qrcode">
          <el-tooltip :content="'' + checkSwitchText[configForm.show_qrcode]" placement="top">
            <el-switch v-model="configForm.show_qrcode" on-color="#13ce66" off-color="#ff4949" :on-value="1" :off-value="0" on-text="" off-text="">
            </el-switch>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="show_share">
          <el-tooltip :content="'' + checkSwitchText[configForm.show_share]" placement="top">
            <el-switch v-model="configForm.show_share" on-color="#13ce66" off-color="#ff4949" :on-value="1" :off-value="0" on-text="" off-text="">
            </el-switch>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="apply">
          <el-tooltip :content="'' + checkSwitchText[configForm.apply]" placement="top">
            <el-switch v-model="configForm.apply" on-color="#13ce66" off-color="#ff4949" :on-value="1" :off-value="0" on-text="" off-text="">
            </el-switch>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="pic_score">
          <el-input v-model.number="configForm.pic_score" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="phone_score">
          <el-input v-model.number="configForm.phone_score" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="pic_price">
          <el-input v-model="configForm.pic_price" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="phone_price">
          <el-input v-model="configForm.phone_price" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('configForm')" style="width: 100%;margin-top: 20px;"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      checkSwitchText: ["", ""],
      configForm: {
        need_pay: 1,
        show_sign: 1,
        show_qrcode: 1,
        show_share: 1,
        apply: 1,
        id: "",
        pic_score: null,
        phone_score: null,
        pic_price: "",
        phone_price: null
      },
      dateRange: "",

      rules: {
        pic_score: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ],
        phone_score: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ],
        pic_price: [{ required: true, message: "" }],
        phone_price: [{ required: true, message: "" }]
      }
    };
  },

  created() {
    this.$api.getConfig(res => {
      this.configForm = res.data.data;
      this.loading = false;
    });
  },

  methods: {
    /*
    * 
    */
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$api.postConfig(this.configForm, res => {
            this.$message({
              type: "success",
              message: "",
              showClose: true
            });
          });
        } else {
          this.$message({
            type: "warning",
            message: "",
            showClose: true
          });
          return false;
        }
      });
    }
  }
};
</script>



