<style scoped>
.breadcrumb {
  margin-bottom: 20px;
}

.banner-list {
  padding: 15px;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.table-list {
  overflow: auto;
  flex: 1;
  min-height: 250px;
}

.banner-operation {
  margin-bottom: 20px;
}

.pages {
  text-align: right;
}

.pre-img-wrap {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.sub-wrap {
  width: 70%;
  text-align: center;
}

.sub-wrap > img {
  max-width: 100%;
}

.sub-wrap > span {
  position: absolute;
  top: 30px;
  right: 30px;
  color: #fff;
  font-size: 30px;
  cursor: pointer;
}

.pre-img-wrap.active {
  display: flex;
}
</style>


<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="banner-list">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="banner-operation">
      <el-button type="primary" @click="addAD"></el-button>
      <!-- <el-button type="primary" @click="openAD"></el-button>
      <el-button type="warning" @click="closeAD"></el-button> -->
    </div>
    <div class="table-list">
      <el-table ref="multipleTable" :data="bannerList" border stripe tooltip-effect="dark" style="width: 100%" @selection-change="handleSelection">
        <!-- <el-table-column type="selection">
        </el-table-column> -->
        <el-table-column label="" prop="type">
          <template slot-scope="scope">
            <span>{{typeText[scope.row.type]}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" prop="url">
          <template slot-scope="scope">
            <img v-if="scope.row.url" :src="scope.row.url" alt="" title="" style="height: 40px; vertical-align: middle; cursor: pointer;" @click="preImgFnc(scope.row.url)">
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="city" align="center">
          <template slot-scope="scope">{{scope.row.city?scope.row.city.name:''}}</template>
        </el-table-column>
        <el-table-column label="" show-overflow-tooltip prop="link_url">
          <template slot-scope="scope">{{scope.row.link_url || ''}}</template>
        </el-table-column>
        <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.state == 1" class="success"></span>
            <span v-else class="warning"></span>
          </template>
        </el-table-column>
        <el-table-column label="" width="200">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="handleChange(scope.$index, scope.row)"></el-button>
            <el-button v-if="scope.row.state == 0" type="primary" size="small" @click="openAD(scope.$index, scope.row)"></el-button>
            <el-button v-else type="danger" size="small" @click="closeAD(scope.$index, scope.row)"></el-button>
            <!-- <el-button size="small" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button> -->
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="page" :page-size="eachPage" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>

    <!--  -->
    <div class="pre-img-wrap" :class="{'active': isPre}">
      <div class="sub-wrap">
        <img :src="preImg" alt="">
        <span @click="closePreImg"></span>
      </div>
    </div>
    <!-- / -->
  </section>
</template>

<script>
export default {
  data() {
    return {
      loading: true,

      //
      isPre: false,
      preImg: "",

      stateText: {
        1: "",
        0: ""
      },
      typeText: {
        1: "",
        2: "",
        3: ""
      },

      // 
      waittingData: [],

      //
      bannerList: [],
      page: 1,
      eachPage: 10,
      count: 0
    };
  },

  created() {
    this.$api.getAdverts("", res => {
      this.bannerList = res.data.data;
      this.count = res.data.count;
      this.loading = false;
    });
  },

  methods: {
    /*
      
    */
    addAD() {
      this.$router.push({ name: "bannerchange", params: { img: null } });
    },

    /*
      
    */
    // handleSelection(selection) {
    //   this.waittingData = selection;
    // },

    /*
      
    */
    openAD(index, row) {
      let postData = row;
      postData.state = 1;
      this.$api.postAdvert(postData, res => {
        this.$api.getAdverts("", res => {
          this.bannerList = res.data.data;
          this.$message({
            type: "success",
            message: ""
          });
        });
      });
    },

    /*
      
    */
    closeAD(index, row) {
      let postData = row;
      postData.state = 0;
      this.$api.postAdvert(postData, res => {
        this.$api.getAdverts("", res => {
          this.bannerList = res.data.data;
          this.$message({
            type: "success",
            message: ""
          });
        });
      });
    },

    /*
    * 
    */
    preImgFnc(img) {
      this.isPre = true;
      this.preImg = img;
    },

    /*
    * 
    */
    closePreImg() {
      this.isPre = false;
    },

    /* 
      
    */
    handleChange(index, row) {
      this.$router.push({ name: "bannerchange", params: { img: row } });
    },

    /*
      
    */
    // handleEdit(index, row) {
    //   this.$router.push({ name: "banneredit", params: { id: row.id } });
    // },

    /*
      
    */
    handleDelete(index, row) {
      this.$operation.tableMessageBox("", () => {
        this.$api.deleteAdvert(row.id, res => {
          this.count--;
          this.bannerList.splice(index, 1);
          this.$message({
            type: "success",
            message: "!"
          });
        });
      });
    },

    /*
      
    */
    handleCurrentChange(page) {
      this.$api.getAdverts({ page }, res => {
        this.bannerList = res.data.data;
      });
    }
  }
};
</script>



