<style>
.breadcrumb {
  margin-bottom: 20px;
}

.back-list-wrap {
  padding: 15px;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.table-list {
  overflow: auto;
  flex: 1;
}

.banner-operation {
  margin-bottom: 20px;
}

.back-list-search {
  margin-top: 20px;
}

.back-list-wrap .el-input-group__prepend {
  width: 200px;
}

.pages {
  text-align: right;
}
</style>


<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="back-list-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="banner-operation">
      <el-button type="primary" @click="addUser"></el-button>
      <el-button type="danger" @click="deleteUser"></el-button>
      <div class="back-list-search">
        <el-select v-model="currentCharacter" clearable placeholder="" @change="characterSelect">
          <el-option v-for="item in characters" :key="item.id" :label="item.name" :value="item.id">
          </el-option>
        </el-select>
        <el-input placeholder="" v-model="selectInput" style="margin-top: 20px;" @keyup.enter.native="selectSearch">
          <el-select v-model="select" slot="prepend" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
          <el-button slot="append" icon="search" @click="selectSearch"></el-button>
        </el-input>
      </div>
    </div>
    <div class="table-list">
      <el-table ref="multipleTable" :data="backList" border stripe tooltip-effect="dark" style="width: 100%" @selection-change="handleSelection">
        <el-table-column type="selection">
        </el-table-column>
        <el-table-column label="ID" prop="id">
        </el-table-column>
        <el-table-column label="" prop="character">
        </el-table-column>
        <el-table-column label="" prop="name">
        </el-table-column>
        <el-table-column prop="password" label="">
        </el-table-column>
        <el-table-column prop="authority" label="" show-overflow-tooltip :formatter="formatAuthority">
        </el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pages">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="page" :page-size="eachPage" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      //
      waittingData: [],

      currentCharacter: "",
      select: "1",
      selectInput: "",
      characters: [
        {
          id: 1,
          name: ""
        },
        {
          id: 2,
          name: ""
        }
      ],

      //
      backList: [
        {
          id: 1,
          name: "",
          character: "",
          password: "123421",
          authority: [1, 2, 3]
        },
        {
          id: 2,
          name: "",
          character: "",
          password: "as1231",
          authority: [1, 2, 6]
        }
      ],
      page: 1,
      eachPage: 100,
      count: 1000
    };
  },

  created() {
    setTimeout(() => {
      this.loading = false;
    }, 200);
  },

  methods: {
    /*
    * 
    */
    formatAuthority(row, column, cellValue) {
      const str = cellValue.reduce((sum, value) => {
        return sum + this.$common.authorities[value].name + "";
      }, "");
      return str;
    },

    /*
      
    */
    addUser() {
      this.$router.push({ name: "backedit", params: { user: null } });
    },

    deleteUser() {
      if (!this.waittingData.length) {
        this.$message({
          type: "warning",
          message: "",
          showClose: true
        });
        return false;
      }
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    /*
      
    */
    characterSelect() {
      console.log(this.currentCharacter);
    },

    selectSearch() {},

    /*
      
    */
    handleSelection(selection) {
      this.waittingData = selection;
    },

    /*
      
    */
    handleEdit(index, row) {
      this.$router.push({ name: "backedit", params: { user: row } });
    },

    /*
      
    */
    handleDelete(index, row) {
      this.$operation.tableMessageBox("", () => {
        this.backList.splice(index, 1);
        this.$message({
          type: "success",
          message: "!"
        });
      });
    },

    /*
      
    */
    handleCurrentChange() {},

    /*
      
    */
    handleSizeChange() {}
  }
};
</script>



