<style>
.report-wrap {
  height: 100%;
  padding: 15px;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 20px;
}

.report-operation {
  margin-bottom: 20px;
  text-align: right;
}

.report-picker > div {
  margin-right: 20px;
}

.report-wrap .table-list {
  flex: 1;
  overflow: auto;
}

.pages {
  text-align: right;
}

.report-wrap .el-input-group__prepend {
  width: 200px;
}
</style>


<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="report-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="report-operation">
      <div style="text-align: right;">
        <el-input placeholder="" v-model="selectInput" style="margin-bottom: 20px; width: 900px;" @keyup.enter.native="selectSearch">
          <el-select v-model="select" slot="prepend" placeholder="">
            <el-option label="" value="username"></el-option>
            <el-option label="" value="user_id"></el-option>
          </el-select>
          <el-button slot="append" icon="search" @click="selectSearch"></el-button>
        </el-input>
      </div>
      <div class="report-picker">
        <!-- <el-select v-model="searchFrom.city" multiple filterable placeholder="">
          <el-option v-for="item in cities" :key="item.id" :label="item.name" :value="item.id">
          </el-option>
        </el-select>
        <el-select v-model="searchFrom.category" multiple filterable placeholder="">
          <el-option v-for="item in categories" :key="item.id" :label="item.name" :value="item.id">
          </el-option>
        </el-select>
        <el-date-picker v-model="searchFrom.dateRange" type="daterange" align="right" placeholder="" :picker-options="dateOptions">
        </el-date-picker> -->
        <el-select v-model="searchFrom.state" filterable placeholder="" @select-change="pickerSearch">
          <el-option v-for="item in reportTypes" :key="item.id" :label="item.name" :value="item.id">
          </el-option>
        </el-select>
        <!-- <el-button type="primary" icon="search" @click="pickerSearch"></el-button> -->
      </div>
    </div>

    <div class="table-list">
      <el-table ref="multipleTable" :data="reportList" border stripe tooltip-effect="dark" style="width: 100%">
        <el-table-column label="" prop="commodity_id">
        </el-table-column>
        <el-table-column label="" prop="commodity_name" show-overflow-tooltip>
          <template slot-scope="scope">
            <el-button type="text" @click="goToInfo(scope.row)">{{scope.row.commodity.title}}</el-button>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">{{scope.row.report_number + ' '}}</template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">{{scope.row.read_number + ' '}}</template>
        </el-table-column>
        <el-table-column label="" prop="created_at"></el-table-column>
        <!-- <el-table-column label="">
          <template slot-scope="scope">
            <span v-if="scope.row.replay === 0"></span>
            <el-button v-else type="text" @click="replayReport(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column> -->
        <el-table-column label="" prop="username">
          <template slot-scope="scope">
            <el-button type="text" @click="checkReportUser(scope.$index, scope.row)">{{scope.row.username}}</el-button>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span class="normal" v-if="scope.row.state == 0"></span>
            <span class="info" v-else-if="scope.row.state == 1"></span>
            <span class="warning" v-else></span>
          </template>
        </el-table-column>
        <el-table-column label="" width="200">
          <template slot-scope="scope">
            <!-- <el-button size="small" type="primary" @click="handleReplay(scope.$index, scope.row)"></el-button> -->
            <el-button v-if="scope.row.state != 1" size="small" type="primary" @click="handle(scope.$index, scope.row)"></el-button>
            <el-button v-if="scope.row.state == 0" size="small" type="warning" @click="handleDelay(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="pages">
      <el-pagination  @current-change="handleCurrentChange" :current-page.sync="page" :page-size="eachPage" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>

  </section>
</template>


<script>
export default {
  data() {
    return {
      loading: true,
      //
      selectInput: "",
      select: "username",

      searchFrom: {
        // city: [],
        // category: [],
        // dateRange: "",
        state: ""
      },
      // cities: [
      //   {
      //     id: 1,
      //     name: ""
      //   },
      //   {
      //     id: 2,
      //     name: ""
      //   },
      //   {
      //     id: 3,
      //     name: ""
      //   }
      // ],

      // categories: [
      //   {
      //     id: 1,
      //     name: ""
      //   },
      //   {
      //     id: 2,
      //     name: ""
      //   },
      //   {
      //     id: 3,
      //     name: ""
      //   }
      // ],

      // dateOptions: this.$common.dateOptions,

      reportTypes: [
        {
          id: 0,
          name: ""
        },
        {
          id: 1,
          name: ""
        },
        {
          id: 2,
          name: ""
        }
      ],

      //
      page: 1,
      eachPage: 10,
      count: 0,
      //
      reportList: []
    };
  },

  created() {
    this.$api.getReports("", res => {
      this.reportList = res.data.data;
      this.loading = false;
      this.count = res.data.count;
    });
  },

  methods: {
    //
    goToInfo(row) {
      let temp = Object.assign(row, row.commodity);
      this.$router.push({
        name: "auditsingle",
        params: { info: temp, state: 3 }
      });
    },
    /*
    * 
    */
    selectSearch() {
      if (this.selectInput) {
        const search = {
          [this.select]: this.selectInput
        };
        this.$api.getReports(search, res => {
          this.reportList = res.data.data;
          this.count = res.data.count;
        });
      } else {
        this.$api.getReports("", res => {
          this.reportList = res.data.data;
          this.count = res.data.count;
        });
      }
    },

    /*
    * 
    */
    pickerSearch() {
      this.$api.getReports(this.searchFrom, res => {
        this.reportList = res.data.data;
        this.count = res.data.count;
      });
    },

    /*
    * 
    */
    replayReport(index, row) {
      // console.log()
    },

    /*
    * 
    */
    checkReportUser(index, row) {
      // this.$message({
      //   type: "success",
      //   message: "test"
      // });
    },

    /*
    * 
    */
    handleReplay(index, row) {
    },

    /*
    * 
    */
    handle(index, row) {
      this.$operation.tableMessageBox("", () => {
        this.$api.changeReport(row.id, { state: 1 }, res => {
          this.reportList[index].state = 1;
          this.$message({
            type: "success",
            message: ""
          });
        });
      });
    },

    /*
    * 
    */
    handleDelay(index, row) {
      this.$operation.tableMessageBox("", () => {
        this.$api.changeReport(row.id, { state: 2 }, res => {
          this.reportList[index].state = 2;
          this.$message({
            type: "success",
            message: ""
          });
        });
      });
    },

    /*
    * 
    */
    handleCurrentChange(page) {
      let getData = {
        page: page,
        state: this.searchFrom.state,
        [this.select]: this.selectInput
      };
      this.$api.getReports(getData, res => {
        this.reportList = res.data.data;
      });
    }
  }
};
</script>



