<style scoped>
.check-in-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 60px;
}

.check-in-form {
  width: 500px;
  margin: 0 auto;
}
</style>

<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="check-in-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="check-in-form">
      <el-form label-position="top" label-width="80px" :model="checkInForm" :rules="rules" ref="checkInForm">
        <!-- <el-form-item label="" prop="status">
          <el-tooltip :content="'' + checkSwitchText[checkInForm.status]" placement="top">
            <el-switch v-model="checkInForm.status" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0" on-text="" off-text="" @change="checkInFnc">
            </el-switch>
          </el-tooltip>
        </el-form-item> -->
        <el-form-item label="" prop="dateRange">
          <el-date-picker v-model="dateRange" type="datetimerange" :picker-options="dateRangeOption" placeholder="" align="left">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="score">
          <el-input v-model.number="checkInForm.score" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('checkInForm')" style="width: 100%;margin-top: 20px;"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      // checkSwitchText: ["", ""],
      checkInForm: {
        id: "",
        // status: "1",
        score: null
      },
      dateRange: "",

      rules: {
        score: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ]
      },

      dateRangeOption: this.$common.dateOptions
    };
  },

  created() {
    this.$api.getCheckIn(res => {
      const data = res.data.data;
      if (data) {
        this.checkInForm = data;
        this.dateRange = [data.start * 1000, data.end * 1000];
      }
      this.loading = false;
    });
  },

  methods: {
    /*
    * 
    */
    // checkInFnc(value) {},

    /*
    * 
    */
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const postData = {
            start: new Date(this.dateRange[0]).toLocaleDateString(),
            end: new Date(this.dateRange[1]).toLocaleDateString(),
            id: this.checkInForm.id,
            score: this.checkInForm.score
          };
          this.$api.postCheckIn(postData, res => {
            this.$message({
              type: "success",
              message: "",
              showClose: true
            });
          });
        } else {
          this.$message({
            type: "warning",
            message: "",
            showClose: true
          });
          return false;
        }
      });
    }
  }
};
</script>



